import { BigNumber, Contract } from 'ethers';
import { useCallback, useEffect, useState } from 'react';

import useCore from '../useCore';
import { StartAndEndDate } from '../../utils/interface';
import { useBlockNumber } from '../../state/application/hooks';
import { LOADING_DEFAULT_DATE_STATE, NON_LOADING_DEFAULT_DATE_STATE } from '../../utils/constants';

const useGetStartAndEndDate = (token: string, id: string) => {
  const [state, setState] = useState<StartAndEndDate>({
    isLoading: true,
    startDate: new Date("October 27, 2021 15:00:00 UTC").getTime() / 1000,
    endDate: new Date("October 28, 2021 11:15:00 UTC").getTime() / 1000,
  });

  const core = useCore();
  const blockNumber = useBlockNumber();

  const fetchValue = useCallback(async () => {
    const contract: Contract = core.getTokenSaleSwapContract(token, id);
    const startDate: BigNumber = await contract.startDate();
    const endDate: BigNumber = await contract.endDate();

    setState({ isLoading: false, startDate: startDate.toNumber(), endDate: endDate.toNumber() });
  }, [core, id, token]);

  useEffect(() => {
    if (core) {
      fetchValue().catch((err) => {
        setState({
          isLoading: false,
          startDate: new Date("October 27, 2021 15:00:00 UTC").getTime() / 1000,
          endDate: new Date("October 28, 2021 11:15:00 UTC").getTime() / 1000,
        });
        console.error(`Failed to fetch start and end date of sale for ${token}: ${err.stack}`)
      });
    } else {
      setState({
        isLoading: false,
        startDate: new Date("October 27, 2021 15:00:00 UTC").getTime() / 1000,
        endDate: new Date("October 28, 2021 11:15:00 UTC").getTime() / 1000
      });
    }
  }, [blockNumber, core, id, fetchValue, token]);

  return state;
};

export default useGetStartAndEndDate;
